<template>
  <div class="login">
    <div class="login-box">
      <div class="login-logo">
        <h3>欢迎登录流域水质预测系统</h3>
      </div>
      <div class="login-box-body" v-if="switchName=='login'">
        <el-form ref="form" :model="form" :rules="rules">
          <el-form-item label prop="username">
            <el-input placeholder="用户名" v-model="form.username"></el-input>
          </el-form-item>
          <el-form-item label prop="password">
            <el-input placeholder="密码" v-model="form.password" type="password" show-password></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="login('form')" @keyup.enter="keyDown(e)">登录</el-button>
          </el-form-item>
          <el-form-item>
            <span class="login-btn" @click="switchClick('register')">注册</span>
          </el-form-item>
        </el-form>
      </div>

      <div class="login-box-body" v-else-if="switchName=='register'">
        <el-form ref="registerForm" :model="registerForm" :rules="rules">
          <el-form-item label prop="username">
            <el-input placeholder="用户名" v-model="registerForm.username"></el-input>
          </el-form-item>
          <el-form-item label prop="password">
            <el-input
              placeholder="密码"
              v-model="registerForm.password"
              type="password"
              show-password
            ></el-input>
          </el-form-item>
          <el-form-item label prop="confirmPassword">
            <el-input
              placeholder="再次输入密码"
              v-model="registerForm.confirmPassword"
              type="password"
              show-password
            ></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="register('registerForm')">注册</el-button>
          </el-form-item>
          <el-form-item>
            <span class="login-btn" @click="switchClick('login')">登录</span>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Login',
  data () {
    return {
      api: this.window.api,
      form: {
        username: '',
        password: '',
      },
      registerForm: {
        username: '',
        password: '',
        confirmPassword: ''
      },
      // 表单校验
      rules: {
        username: [
          { required: true, message: "用户名不能为空", trigger: "blur" }
        ],
        password: [
          { required: true, message: "密码不能为空", trigger: "blur" }
        ],
        confirmPassword: [{ required: true, message: "确认密码不能为空", trigger: "blur" }]
      },
      switchName: 'login'
    }
  },
  mounted () {
    // 绑定监听事件
    window.addEventListener("keydown", this.keyDown);
  },
  destroyed () {
    // 销毁事件
    window.removeEventListener("keydown", this.keyDown, false);
  },
  created () {
  },
  methods: {
    keyDown (e) {
      // 回车则执行登录方法 enter键的ASCII是13
      if (e.keyCode == 13) {
        this.login(); // 定义的登录方法
      }
    },
    switchClick (name) {
      this.switchName = name
    },
    login (formName) {
      let url = this.api
      this.$refs[formName].validate((valid) => {
        if (valid) {
          sessionStorage.setItem('username', this.form.username)
          let userList = {
            username: this.form.username,
            password: this.form.password
          }
          this.$axios
            .post(`${url}/vab-flask-server/login`, userList)
            .then((response) => {
              console.log(response);
              if (response.data.code === 200) {
                this.$router.push("/homePage");
              } else {
                this.$message.warning(response.data.msg)
              }
            })
            .catch((err) => {
              window.console.log(err);
            });
        } else {
          return false;
        }
      });


    },
    // 注册用户
    register () {
      let url = this.api
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let registerList = {
            username: this.registerForm.username,
            password: this.registerForm.password
          }
          this.$axios
            .post(`${url}/vab-flask-server/register`, registerList)
            .then((response) => {
              console.log(response);
              if (response.data.code === 200) {
                this.$message.success(response.data.msg)
              } else {
                this.$message.warning(response.data.msg)
              }
            })
            .catch((err) => {
              window.console.log(err);
            });
        } else {
          return false;
        }
      });
    }
  },
}
</script>

<style>
@import './login.css';
.grid-content {
  border-radius: 4px;
  min-height: 20px;
}
.my-autocomplete li {
  line-height: normal;
  padding: 7px;
}
.name {
  text-overflow: ellipsis;
  overflow: hidden;
}
.addr {
  font-size: 12px;
  color: #b4b4b4;
}

.highlighted .addr {
  color: #ddd;
}
</style>
