<template>
  <div class="app-container">
    <el-row class="nav-search">
      <el-col :span="24">
        <el-select v-model="queryParams.name" placeholder="请选择查询属性" clearable style="width: 240px">
          <el-option
            v-for="name in nameList"
            :key="name.value"
            :label="name.label"
            :value="name.value"
          />
        </el-select>
        <el-input
          class="input-search"
          v-model="queryParams.value"
          placeholder="属性值"
          clearable
          style="width: 240px"
          @keyup.enter.native="handleQuery"
        />
        <el-button type="primary" icon="el-icon-search" size="small" @click="handleQuery">查询</el-button>
      </el-col>
    </el-row>

    <el-table :data="list">
      <el-table-column label="编号" prop="id" width="50"></el-table-column>
      <el-table-column label="用户名" prop="username" show-overflow-tooltip />
      <el-table-column label="状态" prop="ststus"></el-table-column>
      <el-table-column label="权限" prop="role">
        <template slot-scope="scope">
          <el-tag>{{scope.row.role}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" class-name="small-padding fixed-width">
        <template slot-scope="scope">
          <el-button size="mini" type="text" icon="el-icon-edit" @click="handleUpdate(scope.row)">修改</el-button>
        </template>
      </el-table-column>
    </el-table>

    <div class="page-block">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryParams.pageNo"
        :page-sizes="[10, 20, 30, 40,50,100]"
        :page-size="queryParams.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
    <UpdateUser ref="updateUser" @getList="getList" />
  </div>
</template>

<script>
import UpdateUser from './dialog/UpdateUser.vue'
export default {
  name: "UserManagement",
  data () {
    return {
      api: this.window.api,
      // 总条数
      total: 0,
      // 角色表格数据
      list: [],
      // 查询参数
      queryParams: {
        pageNo: 1,
        pageSize: 10,
        name: undefined,
        value: undefined,
      },
      nameList: [{
        value: 'id',
        label: '编号'
      }, {
        value: 'username',
        label: '用户名'
      }, {
        value: 'ststus',
        label: '状态'
      }
      ]
    };
  },
  components: { UpdateUser },
  created () {
    this.getList();
  },
  methods: {
    handleSizeChange (val) {
      this.queryParams.pageSize = val
      this.getList();
      // console.log(`每页 ${val} 条`);
    },
    handleCurrentChange (val) {
      this.queryParams.pageNo = val
      this.getList();
      // console.log(`当前页: ${val}`);
    },
    /** 查询角色列表 */
    getList () {
      let url = this.api
      this.$axios
        .post(`${url}/vab-flask-server/userManagement/getList`, this.queryParams)
        .then((response) => {
          console.log(response);
          if (response.data.code === 200) {
            this.list = response.data.data
            if (response.data.totalCount) {
              this.total = response.data.totalCount
            } else {
              this.total = 0
            }
          } else {
            this.list = []
            this.total = 0
            this.$message.warning(response.data.msg)
          }
        })
        .catch((err) => {
          window.console.log(err);
        });
    },
    /** 搜索按钮操作 */
    handleQuery () {
      this.queryParams.pageNo = 1;
      this.getList();
    },
    /** 修改按钮操作 */
    handleUpdate (row) {
      this.$refs.updateUser.handleClick("修改用户", row)
    },
  }
};
</script>