<template>
  <div class="app-container">
    <!-- 添加或修改角色配置对话框 -->
    <el-dialog :title="title" :visible.sync="open" width="50%" append-to-body>
      <div id="chart" ref="chart" style="width:100%;height: 300px"></div>

      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">关 闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "LineChart",
  data () {
    return {
      api: this.window.api,
      // 是否显示弹出层
      open: false,
      title: '',
      // 表单参数
      form: {},
      data: [],
      // 表单校验
      rules: {
        start: [
          { required: true, message: "开始时间不能为空", trigger: "blur" }
        ],
        name: [
          { required: true, message: "断面名称不能为空", trigger: "blur" }
        ],
        end: [
          { required: true, message: "结束时间不能为空", trigger: "blur" }
        ],
        operation: [
          { required: true, message: "计算方式不能为空", trigger: "blur" }
        ],
      },
      calculationList: ['FCM', 'FCE', 'SSO', 'wqi'],
      timeList: []
    };
  },
  created () {
  },
  methods: {
    handleClick (data) {
      this.open = true
      this.lineCharts(data)
    },
    // 取消按钮
    cancel () {
      this.open = false
    },
    lineCharts (data) {
      let xAxisList = []
      let seriesData = []
      data.filter(item => {
        xAxisList.push(item.date)
        seriesData.push(item.level)
      })
      this.title = data[0].name + '-' + data[0].operation

      // let xAxisList = this.form.data1.date_data.concat(this.form.data1.yuce_data)
      // let seriesData = JSON.parse(JSON.stringify(this.form.data1.date))
      // seriesData.push('-', '-', '-')


      var option = {
        title: {
          text: '等级'
        },
        color: ['#5470c6', '#91cc75'],
        tooltip: {
          trigger: "axis"
        },
        grid: {
          top: "4%",
          left: "2%",
          right: "4%",
          bottom: "0%",
          containLabel: true
        },
        xAxis: [{
          type: "category",
          boundaryGap: false,
          data: xAxisList
        }],
        yAxis: [{
          type: "value"
        }],
        series: [{
          type: "line",
          data: seriesData,
        },
        ]
      }

      this.chartOptions = option
      let that = this
      setTimeout(function () {
        var chart = that.$echarts.init(document.getElementById('chart'));
        chart.setOption(that.chartOptions, true)
        window.onresize = function () {
          chart.resize();
        }
      }, 500)
    },
  }
};
</script>