<template>
  <div class="app-container">
    <el-row class="nav-search">
      <el-col :span="12">
        <el-button type="primary" icon="el-icon-circle-plus" size="small" @click="handleAdd">添加数据</el-button>
      </el-col>
      <el-col :span="12">
        <el-select v-model="queryParams.name" placeholder="请选择查询属性" clearable style="width: 240px">
          <el-option v-for="dict in nameList" :key="dict" :label="dict" :value="dict" />
        </el-select>
        <el-input
          class="input-search"
          v-model="queryParams.value"
          placeholder="属性值"
          clearable
          style="width: 240px"
          @keyup.enter.native="handleQuery"
        />
        <el-button type="primary" icon="el-icon-search" size="small" @click="handleQuery">查询</el-button>
      </el-col>
    </el-row>

    <el-table :data="list">
      <el-table-column label="pH" prop="pH" width="120" />
      <el-table-column label="叶绿素" prop="叶绿素" :show-overflow-tooltip="true" width="150" />
      <el-table-column label="总氮" prop="总氮" :show-overflow-tooltip="true" width="150" />
      <el-table-column label="水温" prop="水温" width="100" />
      <el-table-column label="浊度" prop="浊度" width="100" />
      <el-table-column label="电导率" prop="电导率" width="100" />
      <el-table-column label="蓝绿藻" prop="蓝绿藻" width="100" />
      <el-table-column label="溶解氧" prop="溶解氧" width="100" />
      <el-table-column label="氨氮" prop="氨氮" width="100" />
      <el-table-column label="高锰酸盐指数" prop="高锰酸盐指数" width="100" />
      <el-table-column label="总磷" prop="总磷" width="100" />
      <el-table-column label="测量时间" prop="测量时间" width="100" />
      <el-table-column label="操作" align="center" class-name="small-padding fixed-width">
        <template slot-scope="scope">
          <el-button size="mini" type="text" icon="el-icon-edit" @click="handleUpdate(scope.row)">修改</el-button>
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="page-block">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryParams.pageNo"
        :page-sizes="[10, 20, 30, 40,50,100]"
        :page-size="queryParams.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
    <AddDate ref="addDate" @getList="getList" />
  </div>
</template>

<script>
import AddDate from './dialog/AddDate.vue'
export default {
  name: "DateManagement",
  data () {
    return {
      api: this.window.api,
      // 总条数
      total: 0,
      // 角色表格数据
      list: [],
      // 查询参数
      queryParams: {
        pageNo: 1,
        pageSize: 10,
        name: undefined,
        value: undefined
      },
      nameList: ['监测点', '测量时间', 'pH', '溶解氧', '高锰酸盐指数', '总磷']
    };
  },
  components: { AddDate },
  created () {
    this.getList();
  },
  methods: {
    handleSizeChange (val) {
      this.queryParams.pageSize = val
      this.getList();
      // console.log(`每页 ${val} 条`);
    },
    handleCurrentChange (val) {
      this.queryParams.pageNo = val
      this.getList();
      // console.log(`当前页: ${val}`);
    },
    /** 查询数据列表 */
    getList () {
      let url = this.api
      this.$axios
        .post(`${url}/vab-flask-server/dateManagement/getList`, this.queryParams)
        .then((response) => {
          console.log(response);
          if (response.data.code === 200) {
            this.list = response.data.data
            if (response.data.totalCount) {
              this.total = response.data.totalCount
            } else {
              this.total = 0
            }
          } else {
            this.list = []
            this.total = 0
            this.$message.warning(response.data.msg)
          }
        })
        .catch((err) => {
          window.console.log(err);
        });
    },
    /** 搜索按钮操作 */
    handleQuery () {
      this.queryParams.pageNo = 1;
      this.getList();
    },
    /** 新增按钮操作 */
    handleAdd () {
      this.$refs.addDate.handleClick("添加数据")
    },
    /** 修改按钮操作 */
    handleUpdate (row) {
      this.$refs.addDate.handleClick("修改数据", row)
    },
    /** 删除按钮操作 */
    handleDelete (row) {
      this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let url = this.api
        let data = {
          ids: row.测量时间
        }
        this.$axios
          .post(`${url}/vab-flask-server/dateManagement/doDelete`, data)
          .then((response) => {
            if (response.data.code === 200) {
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
              this.getList()
            } else {
              this.$message({
                type: 'info',
                message: response.data.msg
              })
              // this.$message.warning(response.data.msg)
            }
          })
          .catch((err) => {
            window.console.log(err);
          });
        // this.$message({
        //   type: 'success',
        //   message: '删除成功!'
        // });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
  }
};
</script>