<template>
  <div class="app-container">
    <div class="quality">
      <div class="quality-search">
        <div>
          <el-select v-model="status" placeholder="请选择查询属性" clearable style="width: 240px">
            <el-option v-for="dict in dicts" :key="dict" :label="dict" :value="dict" />
          </el-select>
          <el-select
            v-model="queryParams.value"
            placeholder="请选择查询属性"
            clearable
            style="width: 240px"
          >
            <el-option v-for="dict in valueList" :key="dict" :label="dict" :value="dict" />
          </el-select>

          <el-button type="primary" icon="el-icon-search" size="small" @click="handleQuery">预测</el-button>
        </div>
        <div class="quality-left">
          <el-button type="primary">
            <div>预测时间</div>
            <div v-for="(time,index) in timeList" :key="index">{{time}}</div>
          </el-button>
          <el-button type="primary">
            <div>溶解氧</div>
            <div v-for="(oxygen,index) in oxygenList" :key="index">{{oxygen.toFixed(2)}}</div>
          </el-button>
          <el-button type="primary">
            <div>高锰酸盐指数</div>
            <div
              v-for="(permanganate,index) in permanganateList"
              :key="index"
            >{{permanganate.toFixed(2)}}</div>
          </el-button>
          <el-button type="primary">
            <div>总磷</div>
            <div v-for="(phosphorus,index) in phosphorusList" :key="index">{{phosphorus.toFixed(3)}}</div>
          </el-button>
        </div>
      </div>

      <div class="quality-table">
        <h2 class="quality-title">水质预警信息</h2>
        <el-table :data="yujingList">
          <el-table-column label="预警时间" prop="date" width="200" />
          <el-table-column label="预警原因" :show-overflow-tooltip="true">
            <template scope="scope">
              <span>{{scope.row.data1Yuce< 2 ?"溶解氧低于V类标准,":scope.row.data1Yuce < 3 ?"溶解氧低于IV类标准," :scope.row.data1Yuce< 5 ?"溶解氧低于III类标准," :scope.row.data1Yuce< 6 ?"溶解氧低于II类标准," :scope.row.data1Yuce< 7?"溶解氧低于I类标准,":''}}</span>
              <span>{{scope.row.data2Yuce> 15 ?"高锰酸钾低于V类标准,":scope.row.data2Yuce > 10 ?"高锰酸钾低于IV类标准," :scope.row.data2Yuce> 6 ?"高锰酸钾低于III类标准," :scope.row.data2Yuce> 4 ?"高锰酸钾低于II类标准," :scope.row.data2Yuce> 2?"高锰酸钾低于I类标准,":''}}</span>
              <span>{{scope.row.data3Yuce> .2 ?"总磷低于V类标准":scope.row.data3Yuce > .1 ?"总磷低于IV类标准" :scope.row.data3Yuce> .05 ?"总磷低于III类标准" :scope.row.data3Yuce> .025 ?"总磷低于II类标准" :scope.row.data3Yuce> .01?"总磷低于I类标准":''}}</span>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <div class="quality-chart" v-if="form.data1.date.length>0">
      <div id="chart1" ref="chart1" style="width:33%;height: 300px"></div>
      <div id="chart2" ref="chart2" style="width:33%;height: 300px"></div>
      <div id="chart3" ref="chart3" style="width:33%;height: 300px"></div>
    </div>
    <AddDate ref="addDate" />
  </div>
</template>

<script>
import AddDate from './dialog/AddDate.vue'
export default {
  name: "QualityPrediction",
  data () {
    return {
      api: this.window.api,
      // 总条数
      total: 0,
      // 查询参数
      queryParams: {
        value: undefined,
      },
      status: '东圳水库',
      dicts: ["东圳水库"],
      valueList: ['OnlineESN', 'OfflineESN', 'MIC-OnlineESN', 'MIC-OfflineESN'],
      form: {
        code: 200,
        data1: {
          date: [],
          date_data: [],
          yuce: [], yuce_data: []
        },
        data2: {
          date: [],
          date_data: [],
          yuce: [],
          yuce_data: []
        },
        data3: {
          date: [],
          date_data: [],
          yuce: [],
          yuce_data: []
        },
        date: []
      },
      timeList: ['-', '-', '-'],
      oxygenList: [0.00, 0.00, 0.00],
      permanganateList: [0.00, 0.00, 0.00],
      phosphorusList: [0.000, 0.000, 0.000,],
      yujingList: [],
      chartOptions: {},
      chartOptions2: {},
      chartOptions3: {}
    }
  },
  components: { AddDate },
  created () {
  },
  methods: {
    /** 查询列表 */
    getList () {
      let url = this.api
      this.$axios
        .post(`${url}/vab-flask-server/dateManagement/getList1`, this.queryParams)
        .then((response) => {
          console.log(response);
          if (response.data.code === 200) {
            this.form = response.data
            this.timeList = this.form.data1.yuce_data
            this.oxygenList = this.form.data1.yuce
            this.permanganateList = this.form.data2.yuce
            this.phosphorusList = this.form.data3.yuce

            this.form.date.filter((item, index) => {
              this.yujingList.push({
                date: item,
                data1Yuce: this.form.data1.yuce[index],
                data2Yuce: this.form.data2.yuce[index],
                data3Yuce: this.form.data3.yuce[index],
              })
            })

            if (this.form.data1.date.length > 0) {
              this.lineCharts()
              this.lineCharts2()
              this.lineCharts3()
            }
          } else {
            this.form = {
              code: 200,
              data1: {
                date: [],
                date_data: [],
                yuce: [], yuce_data: []
              },
              data2: {
                date: [],
                date_data: [],
                yuce: [],
                yuce_data: []
              },
              data3: {
                date: [],
                date_data: [],
                yuce: [],
                yuce_data: []
              },
              date: []
            }
            this.timeList = []
            this.oxygenList = []
            this.permanganateList = []
            this.phosphorusList = []
            this.yujingList = []
            this.$message.warning(response.data.msg)
          }
        })
        .catch((err) => {
          window.console.log(err);
        });
    },
    /** 搜索按钮操作 */
    handleQuery () {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    lineCharts () {
      let xAxisList = this.form.data1.date_data.concat(this.form.data1.yuce_data)
      let seriesData = JSON.parse(JSON.stringify(this.form.data1.date))
      seriesData.push('-', '-', '-')

      let yuDate = JSON.parse(JSON.stringify(this.form.data1.yuce))
      yuDate.unshift('-', '-', '-', '-', '-', '-', this.form.data1.date[6])

      var option = {
        title: {
          text: "溶解氧"
        },
        color: ['#5470c6', '#91cc75'],
        tooltip: {
          trigger: "axis"
        },
        grid: {
          top: "4%",
          left: "2%",
          right: "4%",
          bottom: "0%",
          containLabel: true
        },
        xAxis: [{
          type: "category",
          boundaryGap: false,
          data: xAxisList
        }],
        yAxis: [{
          type: "value",
          min: 4,
          max: 9
        }],
        series: [{
          name: "预测结果",
          type: "line",
          data: yuDate,
          itemStyle: {
            normal: {
              lineStyle: {
                color: "red"
              }
            }
          }
        },
        {
          name: "观测数据",
          type: "line",
          data: seriesData
        }
        ]
      }

      this.chartOptions = option
      let that = this
      setTimeout(function () {
        var chart1 = that.$echarts.init(document.getElementById('chart1'));
        chart1.setOption(that.chartOptions, true)
        window.onresize = function () {
          chart1.resize();
        }
      }, 500)
    },
    lineCharts2 () {
      let xAxisList = this.form.data2.date_data.concat(this.form.data2.yuce_data)
      let seriesData = JSON.parse(JSON.stringify(this.form.data2.date))
      seriesData.push('-', '-', '-')

      let yuDate = JSON.parse(JSON.stringify(this.form.data2.yuce))
      yuDate.unshift('-', '-', '-', '-', '-', '-', this.form.data2.date[6])

      var option = {
        title: {
          text: "高锰酸盐指数"
        },
        color: ['#5470c6', '#91cc75'],
        tooltip: {
          trigger: "axis"
        },
        grid: {
          top: "4%",
          left: "2%",
          right: "4%",
          bottom: "0%",
          containLabel: true
        },
        xAxis: [{
          type: "category",
          boundaryGap: false,
          data: xAxisList
        }],
        yAxis: [{
          type: "value",
          min: .5,
          max: 1.8
        }],
        series: [{
          name: "预测结果",
          type: "line",
          data: yuDate,
          itemStyle: {
            normal: {
              lineStyle: {
                color: "red"
              }
            }
          }
        },
        {
          name: "观测数据",
          type: "line",
          data: seriesData
        }
        ]
      }

      this.chartOptions2 = option
      let that = this
      setTimeout(function () {
        var chart2 = that.$echarts.init(document.getElementById('chart2'));
        chart2.setOption(that.chartOptions2, true)
        window.onresize = function () {
          chart2.resize();
        }
      }, 500)
    },
    lineCharts3 () {
      let xAxisList = this.form.data3.date_data.concat(this.form.data3.yuce_data)
      let seriesData = JSON.parse(JSON.stringify(this.form.data3.date))
      seriesData.push('-', '-', '-')
      console.log(seriesData);
      let yuDate = JSON.parse(JSON.stringify(this.form.data3.yuce))
      yuDate.unshift('-', '-', '-', '-', '-', '-', this.form.data3.date[6])
      console.log(yuDate);
      var option = {
        title: {
          text: "总磷"
        },
        color: ['#5470c6', '#91cc75'],
        tooltip: {
          trigger: "axis"
        },
        grid: {
          top: "4%",
          left: "2%",
          right: "4%",
          bottom: "0%",
          containLabel: true
        },
        xAxis: [{
          type: "category",
          boundaryGap: false,
          data: xAxisList
        }],
        yAxis: [{
          type: "value",
          min: .015,
          max: .033
        }],
        series: [{
          name: "预测结果",
          type: "line",
          data: yuDate,
          itemStyle: {
            normal: {
              lineStyle: {
                color: "red"
              }
            }
          }
        },
        {
          name: "观测数据",
          type: "line",
          data: seriesData
        }
        ]
      }

      this.chartOptions3 = option
      let that = this
      setTimeout(function () {
        var chart3 = that.$echarts.init(document.getElementById('chart3'));
        chart3.setOption(that.chartOptions3, true)
        window.onresize = function () {
          chart3.resize();
        }
      }, 500)
    }
  }
};
</script>