<template>
  <div class="app-container">
    <!-- 添加或修改角色配置对话框 -->
    <el-dialog class="lineChart" title="水质评价" :visible.sync="open" width="50%" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="150px">
        <el-form-item label="断面名称" prop="name">
          <el-select
            v-model="form.name"
            placeholder="请选择断面名称"
            clearable
            filterable
            @change="getTime"
          >
            <el-option
              v-for="(dict,index) in data"
              :key="index"
              :label="dict.label"
              :value="dict.label"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="开始时间" prop="start">
          <!-- <el-input v-model="form.开始时间" placeholder="请输入开始时间" /> -->
          <el-select v-model="form.start" placeholder="请选择开始时间" clearable>
            <el-option
              v-for="(dict,index) in timeList"
              :key="index"
              :label="dict.label"
              :value="dict.label"
            />
          </el-select>
        </el-form-item>

        <el-form-item label="结束时间" prop="end">
          <!-- <el-input v-model="form.结束时间" placeholder="请输入结束时间" /> -->
          <el-select v-model="form.end" placeholder="请选择结束时间" clearable>
            <el-option
              v-for="(dict,index) in timeList"
              :key="index"
              :label="dict.label"
              :value="dict.label"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="计算方式" prop="operation">
          <el-select v-model="form.operation" placeholder="请选择计算方式" clearable>
            <el-option
              v-for="(dict,index) in calculationList"
              :key="index"
              :label="dict"
              :value="dict"
            />
          </el-select>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm('form')">水质评价</el-button>
        <el-button @click="cancel">取 消</el-button>
      </div>
      <LineChart ref="lineChart" />
    </el-dialog>
  </div>
</template>

<script>
import LineChart from './LineChart.vue'
export default {
  name: "Evaluate",
  data () {
    return {
      api: this.window.api,
      // 是否显示弹出层
      open: false,
      // 表单参数
      form: {},
      data: [],
      // 表单校验
      rules: {
        start: [
          { required: true, message: "开始时间不能为空", trigger: "blur" }
        ],
        name: [
          { required: true, message: "断面名称不能为空", trigger: "blur" }
        ],
        end: [
          { required: true, message: "结束时间不能为空", trigger: "blur" }
        ],
        operation: [
          { required: true, message: "计算方式不能为空", trigger: "blur" }
        ],
      },
      calculationList: ['FCM', 'FCE', 'SSO', 'wqi'],
      timeList: []
    };
  },
  created () {
    this.getNames()
  },
  components: { LineChart },
  methods: {
    handleClick () {
      this.open = true
    },
    // 取消按钮
    cancel () {
      this.open = false
      this.form = {}
    },
    getTime (val) {
      console.log(val);
      this.data.filter(item => {
        if (item.label == val) {
          this.timeList = item.children
        }
      })
    },
    /** 提交按钮 */
    submitForm (formName) {
      let url = this.api
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$axios
            .post(`${url}/vab-flask-server/yingManagement/doRun`, this.form)
            .then((response) => {
              console.log(response);
              if (response.data.code === 200) {

                this.cancel()
                this.$refs.lineChart.handleClick(response.data.data)

                // this.$message.success(response.data.msg)
                // this.$emit('getList')
              } else {
                this.$message.warning(response.data.msg)
              }
            })
            .catch((err) => {
              window.console.log(err);
            });
        } else {
          return false;
        }
      });
    },
    getNames () {
      let url = this.api
      this.$axios
        .get(`${url}/vab-flask-server/yingManagement/getNames`,)
        .then((response) => {
          console.log(response);
          if (response.data.code === 200) {
            this.data = response.data.data
            // this.data.filter(item => {
            //   item.disabled = true
            //   item.children.unshift({
            //     label: '所有时间',
            //     id: item.id + '-0'
            //   })
            // })
          } else {
            this.$message.warning(response.data.msg)
          }
        })
        .catch((err) => {
          window.console.log(err);
        });
    },
  }
};
</script>