<template>
  <header>
    <div class="header-top">
      <div class="logo">
        <i v-if="isCollapse" class="el-icon-s-unfold" @click="handleOpen"></i>
        <i v-else class="el-icon-s-fold" @click="handleClose"></i>
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item>{{breadTitle}}</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <div class="infor">
        <div class="changePsw">
          <el-dropdown trigger="click">
            <span class="el-dropdown-link">
              <i class="el-icon-user-solid"></i>
              {{username}}
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <span @click="openPersonal">个人中心</span>
              </el-dropdown-item>
              <el-dropdown-item>
                <span @click="exit()">退出登录</span>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
    <div class="header-bottom">
      <el-tabs v-model="homeValueChild" type="card" @tab-click="changHomePage">
        <el-tab-pane label="首页" name="homePage"></el-tab-pane>
      </el-tabs>
      <el-tabs
        v-model="editableTabsValueChild"
        type="card"
        closable
        @tab-click="changeTab"
        @tab-remove="removeTab"
        v-if="editableTabs.length>0"
      >
        <el-tab-pane v-for="(item, index) in editableTabs" :key="item" :label="item" :name="item"></el-tab-pane>
      </el-tabs>
    </div>
  </header>
</template>

<script>
export default {
  name: "headerone",
  data () {
    return {
      api: this.window.api,
      username: sessionStorage.getItem('username'),
      editableTabsValueChild: '',
      homeValueChild: 'homePage'
    };
  },
  props: {
    isCollapse: {
      type: Boolean,
      default: false
    },
    editableTabs: {
      type: Array,
      default: []
    },
    breadTitle: {
      type: String,
      default: '首页'
    }
  },
  created () {
  },
  methods: {
    handleOpen () {
      this.$emit('handleOpen')
    },
    handleClose () {
      this.$emit('handleClose')
    },
    changHomePage () {
      this.$emit('changHomePage')
    },
    getTabVal (val) {
      this.editableTabsValueChild = val
    },
    changeTab (val) {
      this.$emit('changeTab', val.name)
    },
    changeTabChild (value) {
      this.editableTabsValueChild = value.editableTabsValue
      this.homeValueChild = value.homeValue
    },
    removeTab (targetName) {
      this.$emit('removeTab', targetName, this.editableTabsValueChild)
    },
    openPersonal () {
      this.$router.push('/personalCenter')
    },
    exit () {
      this.$confirm('是否退出登录流域水质预测系统?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let url = this.api
        this.$axios
          .post(`${url}/vab-flask-server/logout`)
          .then((response) => {
            if (response.data.code == 200) {
              this.$message.success('退出成功')
            }
          })
          .catch((err) => {
            window.console.log(err);
          });

        this.$router.push("/");
        // var storage = window.sessionStorage;
        // storage.clear()
      }).catch(() => {
      });
    }
  },
};
</script>

