let startApp = function () {
    axios.get('./config.json').then((res) => {
      // 基础地址
        Vue.prototype.window = res.data;
      new Vue({
        router,
        store,
        render: h => h(App)
    }).$mount('#app')
    })
  }
  
  startApp()
  
