  props: ['isCollapse'],
  // mounted () {
  //   this.activeIndex = this.$route.path;
  // },
  watch: {
    $route: {
      handler () {
        this.activeIndex = this.$route.path;
      },
      // 代表在wacth里声明了firstName这个方法之后立即先去执行handler方法，如果设置了false，那么效果和上边例子一样
      immediate: true,
      deep: true, //true 深度监听
    },
  },
  methods: {
    handleOpen (key, keyPath) {
      console.log(1);
      console.log(key, keyPath);
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath);
    },
    changHomePage (val) {
      this.$emit('changeBreadTitle', val)
      this.$emit('changHomePage')
    },
    addTab (val) {
      this.$emit('changeBreadTitle', val.title)
      this.$emit('addTab', val.title)
      console.log(val);
    },
    changeRoute (name) {
      this.menuList.filter(item => {
        if (name == item.title) {
          this.$router.push(item.route)
        }
      })
    }
  },
};
