<template>
  <div class="app-container">
    <!-- 添加或修改角色配置对话框 -->
    <el-dialog :title="title" :visible.sync="open" width="50%" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="150px">
        <el-form-item label="pH" prop="pH">
          <el-input v-model="form.pH" placeholder="请输入pH" />
        </el-form-item>
        <el-form-item label="叶绿素" prop="叶绿素">
          <el-input v-model="form.叶绿素" placeholder="请输入叶绿素" />
        </el-form-item>
        <el-form-item label="总氮" prop="总氮">
          <el-input v-model="form.总氮" placeholder="请输入总氮" />
        </el-form-item>
        <el-form-item label="水温" prop="水温">
          <el-input v-model="form.水温" placeholder="请输入水温" />
        </el-form-item>
        <el-form-item label="浊度" prop="浊度">
          <el-input v-model="form.浊度" placeholder="请输入浊度" />
        </el-form-item>
        <el-form-item label="电导率" prop="电导率">
          <el-input v-model="form.电导率" placeholder="请输入电导率" />
        </el-form-item>
        <el-form-item label="蓝绿藻" prop="蓝绿藻">
          <el-input v-model="form.蓝绿藻" placeholder="请输入蓝绿藻" />
        </el-form-item>
        <el-form-item label="溶解氧" prop="溶解氧">
          <el-input v-model="form.溶解氧" placeholder="请输入溶解氧" />
        </el-form-item>
        <el-form-item label="氨氮" prop="氨氮">
          <el-input v-model="form.氨氮" placeholder="请输入氨氮" />
        </el-form-item>
        <el-form-item label="高锰酸盐指数" prop="高锰酸盐指数">
          <el-input v-model="form.高锰酸盐指数" placeholder="请输入高锰酸盐指数" />
        </el-form-item>
        <el-form-item label="总磷" prop="总磷">
          <el-input v-model="form.总磷" placeholder="请输入总磷" />
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm('form')">确 定</el-button>
        <el-button @click="cancel">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "AddDate",
  data () {
    return {
      // 弹出层标题
      title: "",
      // 是否显示弹出层
      open: false,
      // 表单参数
      form: {
        监测点: ''
      },
      // 表单校验
      rules: {
        pH: [
          { required: true, message: "pH不能为空", trigger: "blur" }
        ],
        叶绿素: [
          { required: true, message: "叶绿素不能为空", trigger: "blur" }
        ],
        总氮: [
          { required: true, message: "总氮不能为空", trigger: "blur" }
        ],
        水温: [
          { required: true, message: "水温不能为空", trigger: "blur" }
        ],
        浊度: [
          { required: true, message: "浊度不能为空", trigger: "blur" }
        ],
        电导率: [
          { required: true, message: "电导率不能为空", trigger: "blur" }
        ],
        蓝绿藻: [
          { required: true, message: "蓝绿藻不能为空", trigger: "blur" }
        ],
        溶解氧: [
          { required: true, message: "溶解氧不能为空", trigger: "blur" }
        ],
        氨氮: [
          { required: true, message: "氨氮不能为空", trigger: "blur" }
        ],
        高锰酸盐指数: [
          { required: true, message: "高锰酸盐指数不能为空", trigger: "blur" }
        ],
        总磷: [
          { required: true, message: "总磷不能为空", trigger: "blur" }
        ]
      },
      api: this.window.api,

    };
  },
  created () {
  },
  methods: {
    handleClick (title, form) {
      this.open = true
      this.title = title
      console.log(form);
      if (form) {
        this.form = JSON.parse(JSON.stringify(form))
      } else {
        this.form = {
          pH: '',
          叶绿素: '',
          总氮: '',
          水温: '',
          浊度: '',
          电导率: '',
          蓝绿藻: '',
          溶解氧: '',
          氨氮: '',
          高锰酸盐指数: '',
          总磷: '',
        }


        this.form.监测点 = '东圳水库'
      }
    },
    // 取消按钮
    cancel () {
      this.open = false;
      this.form = {
        pH: '',
        叶绿素: '',
        总氮: '',
        水温: '',
        浊度: '',
        电导率: '',
        蓝绿藻: '',
        溶解氧: '',
        氨氮: '',
        高锰酸盐指数: '',
        总磷: ''
      }
    },
    /** 提交按钮 */
    submitForm (formName) {
      let url = this.api
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$axios
            .post(`${url}/vab-flask-server/dateManagement/doEdit`, this.form)
            .then((response) => {
              if (response.data.code === 200) {
                this.cancel()
                this.$message.success(response.data.msg)
                this.$emit('getList')
              } else {
                this.$message.warning(response.data.msg)
              }
            })
            .catch((err) => {
              window.console.log(err);
            });
        } else {
          return false;
        }
      });
    },
  }
};
</script>