<template>
  <div class="app-container">
    <el-tabs :tab-position="tabPosition">
      <el-tab-pane label="信息修改">
        <el-form ref="form" :model="form" :rules="rules" label-width="80px">
          <el-form-item label="用户名" prop="username" required>
            <el-input v-model="form.username"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input v-model="form.password" placeholder="设置密码提示，没有输入密码默认不修改"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="editPerson('form')">确认修改</el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <!-- <el-tab-pane label="头像设置">配置管理</el-tab-pane> -->
    </el-tabs>
  </div>
</template>

<script>
export default {
  name: "UserManagement",
  data () {
    return {
      api: this.window.api,
      tabPosition: 'left',
      // 表单数据
      form: {
        name: '',
        password: undefined
      },
      // 表单规则
      rules: {
        username: [
          { required: true, message: "用户名不能为空", trigger: "blur" }
        ],
      },
    };
  },
  created () {
    this.getList();
  },
  methods: {
    /** 查询个人信息 */
    getList () {
      let url = this.api
      this.$axios
        .post(`${url}/vab-flask-server/userinfo_`)
        .then((response) => {
          console.log(response);
          if (response.data.code === 200) {
            this.form = response.data.form
          } else {
            this.form = {}
            this.$message.warning(response.data.msg)
          }
        })
        .catch((err) => {
          window.console.log(err);
        });
    },
    // 修改信息
    editPerson (formName) {
      let url = this.api
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$axios
            .post(`${url}/vab-flask-server/edit`, this.form)
            .then((response) => {
              console.log(response);
              if (response.data.code === 200) {
                this.$message.success(response.data.msg)
                this.getList()
              } else {
                this.$message.warning(response.data.msg)
              }
            })
            .catch((err) => {
              window.console.log(err);
            });
        } else {
          return false;
        }
      });
    }
  }
};
</script>