<template>
  <div class="app-container">
    <el-row class="nav-search">
      <el-col :span="12" class="ying">
        <el-upload
          ref="uploadRef"
          class="upload-demo"
          :action="doUpload"
          :before-upload="beforeUpload"
          accept=".xlsx, .xls"
        >
          <el-button size="small" type="primary">上传数据</el-button>
        </el-upload>
        <el-button size="small" @click="download">下载模板</el-button>
      </el-col>
      <el-col :span="12">
        <el-button type="primary" icon="el-icon-search" size="small" @click="handleQuery">评价</el-button>
      </el-col>
    </el-row>

    <div class="quality-content">
      <el-tree
        :data="data"
        show-checkbox
        node-key="id"
        :check-on-click-node="true"
        :expand-on-click-node="false"
        :props="defaultProps"
        highlight-current
        @check-change="handleNodeClick"
        ref="treeForm"
      ></el-tree>
      <div class="quality-table">
        <el-table :data="list">
          <!-- <el-table-column label="断面名称" prop="roleId" width="120" />
          -->
          <el-table-column label="测量时间" prop="测量时间" :show-overflow-tooltip="true" width="150" />
          <el-table-column label="溶解氧" prop="溶解氧" :show-overflow-tooltip="true" />
          <el-table-column label="高锰酸钾指数" prop="高锰酸盐指数" />
          <el-table-column label="五日生化需氧量" prop="五日生化需氧量" />
          <el-table-column label="氨氮" prop="氨氮" />
          <el-table-column label="总磷" prop="总磷" />
          <el-table-column label="FCM" prop="FCM" />
          <el-table-column label="FCE" prop="FCE" />
          <el-table-column label="WQI" prop="WQI" />
          <el-table-column label="SSO" prop="SSO" />
        </el-table>
        <div class="page-block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryParams.pageNo"
            :page-sizes="[10, 20, 30, 40,50,100]"
            :page-size="queryParams.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
          ></el-pagination>
        </div>
      </div>
    </div>
    <Evaluate ref="evaluate" />
  </div>
</template>

<script>
import Evaluate from './dialog/Evaluate.vue'
export default {
  name: "DateManagement",
  data () {
    return {
      api: this.window.api,
      // 总条数
      total: 0,
      // 表格数据
      list: [],
      // 查询参数
      queryParams: {
        pageNo: 1,
        pageSize: 10,
        name: undefined,
        date: undefined,
      },
      dicts: [],
      data: [],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      fileList: [],
      doUpload: this.window.api + '/vab-flask-server/yingManagement/uploadFile'
    };
  },
  created () {
    this.getNames();

  },
  components: { Evaluate },
  methods: {
    handleNodeClick (data, checked, node) {
      if (data.children) {
        this.queryParams.name = data.label
      }
      if (checked) {
        if (!data.children) {
          if (data.label == '所有时间') {
            this.queryParams.date = undefined
          } else {
            this.queryParams.date = data.label
          }
        }
        this.$refs.treeForm.setCheckedKeys([data.id])
        this.queryParams.pageNo = 1
        this.getList()
      }
    },
    handleSizeChange (val) {
      this.queryParams.pageSize = val
      this.getList();
      // console.log(`每页 ${val} 条`);
    },
    handleCurrentChange (val) {
      this.queryParams.pageNo = val
      this.getList();
      // console.log(`当前页: ${val}`);
    },
    beforeUpload (file) {
      this.files = file;
      this.fileName = file.name;
      // this.fileList.push({
      //   name: file.name
      // })
      if (this.fileName == "") {
        this.$message.warning("请选择附件上传！");
        return false;
      }

      let fileFormData = new FormData();
      fileFormData.append("file", this.files, this.fileName);
      let url = this.api;
      this.$axios.post(`${url}/vab-flask-server/yingManagement/uploadFile`, fileFormData).then((response) => {
        if (response.data.code == 200) {
          this.$message.success('上传成功')
          this.getNames();
        } else {
          this.$message.warning('上传失败')
        }
      })
      this.$refs.uploadRef.clearFiles()
      return false; // 返回false不会自动上传

    },
    /** 查询列表 */
    getList () {
      this.list = []
      this.total = 0
      let url = this.api
      this.$axios
        .post(`${url}/vab-flask-server/yingManagement/getList`, this.queryParams)
        .then((response) => {
          console.log(response);
          if (response.data.code === 200) {
            this.list = response.data.data
            if (response.data.totalCount) {
              this.total = response.data.totalCount
            } else {
              this.total = 0
            }
          } else {
            this.list = []
            this.total = 0
            this.$message.warning(response.data.msg)
          }
        })
        .catch((err) => {
          window.console.log(err);
        });
    },
    getNames () {
      let url = this.api
      this.$axios
        .get(`${url}/vab-flask-server/yingManagement/getNames`,)
        .then((response) => {
          console.log(response);
          if (response.data.code === 200) {
            this.data = response.data.data
            this.data.filter(item => {
              item.disabled = true
              item.children.unshift({
                label: '所有时间',
                id: item.id + '-0'
              })
            })
          } else {
            this.$message.warning(response.data.msg)
          }
        })
        .catch((err) => {
          window.console.log(err);
        });
    },
    /** 搜索按钮操作 */
    handleQuery () {
      this.$refs.evaluate.handleClick()
      // this.queryParams.pageNo = 1;
      // this.getList();
    },
    download () {
      let url = this.api
      window.location.href = `${url}/vab-flask-server/yingManagement/downloadFile`
    }
  }
};
</script>