<template>
  <div class="home">
    <el-container>
      <el-aside :width="isCollapse?'64px':'256px'">
        <Aside
          ref="aside"
          :isCollapse="isCollapse"
          @addTab="addTab"
          @changHomePage="changHomePage"
          @changeBreadTitle="changeBreadTitle"
        />
      </el-aside>
      <el-container>
        <el-header height="115px">
          <Header
            ref="homeHeader"
            :isCollapse="isCollapse"
            @handleOpen="handleOpen"
            @handleClose="handleClose"
            :editableTabs="editableTabs"
            @removeTab="removeTab"
            @changHomePage="changHomePage"
            @changeTab="changeTab"
            :breadTitle="breadTitle"
          />
        </el-header>
        <el-main>
          <div class="home-main">
            <Router-view></Router-view>
          </div>
        </el-main>
        <el-footer>
          <div>Copyright 流域水质预测系统-project 2023</div>
        </el-footer>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import Header from "./Header";
import Aside from "./Aside";
import Footer from './Footer'
export default {
  name: "home",
  data () {
    return {
      isCollapse: false,
      editableTabs: [],
      breadTitle: '首页'
    }
  },
  components: {
    Header,
    Aside,
    Footer
  },
  mounted () {
    console.log(this.$route);
    this.editableTabs = []

    if (this.$route.path != '/homePage') {
      this.addTab(this.$route.name)
    } else {
      let data = {
        editableTabsValue: '',
        homeValue: 'homePage'
      }
      this.$refs.homeHeader.changeTabChild(data)
    }
  },
  watch: {
    $route: {
      handler () {
        if (sessionStorage.getItem('homeRouteName')) {
          this.addTab(sessionStorage.getItem('homeRouteName'))
          // sessionStorage.removeItem('homeRoute')
          sessionStorage.removeItem('homeRouteName')
        }
      },
      // 代表在wacth里声明了firstName这个方法之后立即先去执行handler方法，如果设置了false，那么效果和上边例子一样
      immediate: true,
      deep: true, //true 深度监听
    },
  },
  methods: {
    handleOpen () {
      this.isCollapse = false
    },
    handleClose () {
      this.isCollapse = true
    },
    changeBreadTitle (val) {
      this.breadTitle = val
    },
    changHomePage () {
      let data = {
        editableTabsValue: '',
        homeValue: 'homePage'
      }
      this.$refs.homeHeader.changeTabChild(data)
    },
    changeTab (val) {
      this.editableTabsValue = val
      let data = {
        editableTabsValue: val,
        homeValue: ''
      }
      console.log(val);
      this.$refs.homeHeader.changeTabChild(data)
      this.$refs.aside.changeRoute(val)

    },
    addTab (title) {
      this.isHome = false
      if (this.editableTabs.indexOf(title) == -1) {
        this.editableTabs.push(title);
        console.log(this.editableTabs);
      }
      let data = {
        editableTabsValue: title,
        homeValue: ''
      }
      console.log(data);
      this.$refs.homeHeader.changeTabChild(data)
    },
    removeTab (targetName,) {
      let tabs = this.editableTabs;
      let activeName = this.editableTabsValue;
      if (activeName === targetName) {
        tabs.forEach((tab, index) => {
          if (tab === targetName) {
            let nextTab = tabs[index + 1] || tabs[index - 1];
            console.log(nextTab);

            if (nextTab) {
              activeName = nextTab;
              console.log(nextTab);
              // this.$router.push('/homePage')
              this.$refs.homeHeader.getTabVal(nextTab)
              this.$refs.aside.changeRoute(nextTab)
            }
          }
        });
      }
      this.editableTabs = tabs.filter(tab => tab !== targetName);
      if (this.editableTabs.length < 1) {
        this.$router.push('/homePage')
        let data = {
          editableTabsValue: '',
          homeValue: 'homePage'
        }
        this.$refs.homeHeader.changeTabChild(data)
      }

    },
  }
};
</script>

