.home-main {
  margin: 25px auto;
  width: calc(100% - 40px);
  height: calc(100vh - 225px);
}
.el-main,
.el-footer,
.el-header {
  padding: 0 !important;
}
.el-main {
  background-color: #f6f8f9;
  /* padding: 20px; */
}
.el-footer {
  height: 60px;
  overflow: hidden;
}
.el-footer div {
  width: 100%;
  color: rgba(0, 0, 0, 0.45);
  text-align: center;
  border-top: 1px dashed #dcdfe6;
  line-height: 60px;
}
</style>
